







import Vue from "vue";
import axios, { AxiosResponse } from "axios";
import { tree as TreeView } from "vued3tree";
import VueRouter from "vue-router";
//import HelloWorld from "../components/HelloWorld.vue"; // @ is an alias to /src

export default Vue.extend({
  name: "DependencyView",
  data() {
    return {
      showTree: false,
      loadingText: 'Loading Dependencies',
      tree: {}
    };
  },
  methods: {
    async getPackageDependencies() {
      const { packageName, version } = this.$route.params;

      try {
        const result: AxiosResponse = await axios.get(`/api/package/${packageName}/${version}`)

        this.tree = result.data;
        this.showTree = true;

      } catch (error) {
        this.loadingText = error.response.data;
      }
    }
  },
  mounted() {
    this.getPackageDependencies();
  },
  components: {
    TreeView
  }
});
